<template>
	<view>
		<u-toast ref="uToast"></u-toast>
		<view class="searchBtn">
			<u-search placeholder="输入商品名称" inputAlign="center" :showAction="true" :animation="true" shape="round"
				:clearabled="true" @click="godetailSearch()"></u-search>
			<!-- <text class="screening" @click="handleScreening">筛选</text> -->
		</view>
		<view class="total" v-if="orderDetaillist.length>0">
			<view class="totalColor">
				订单编号：{{orderNo}},共{{totalNum}}条数据
			</view>
			<view class="detailContent" v-for="(item,index) in orderDetaillist" :key="index">
				<view class="detailItem">
					<view class="detailSingle">
						<text class="detailHeader">商品名称：</text>
						<text class="detailValue">{{item.productName}}</text>
					</view>
					<view class="detailSingle">
						<text class="detailHeader">商品单价：</text>
						<text class="detailValue">{{item.price1}}</text>
					</view>
					<view class="detailSingle">
						<text class="detailHeader">订单数量：</text>
						<text class="detailValue">{{item.qtyordered}}</text>
					</view>
					<!-- <button type="primary" size="mini" @click="handleQuality(item)">质检预览</button> -->
				</view>
			</view>
		</view>
		<view class="empty" v-else>
			暂无数据
		</view>
	</view>
</template>
<script>
	export default {
		data() {
			return {
				// 订单详情接口传参
				orderNo:"",
				// 订单详情数组
				orderDetaillist:[],
				// 订单总数
				totalNum:null,
				queryParams:{
					pageNum:1,
					pageSize:10,
				}
			}
		},
		onLoad(options){
			console.log(options);
			this.orderNo=options.orderNo;
			this.detailList();
		},
		onShow(){
			// this.detailList();
		},
		methods:{
			// 质检预览
			// handleQuality(items){
			// 	let orderNum=items.orderNo;
			// 	this.request('orderapi/getReport',{orderNo:orderNum},'GET',{}).then((res)=>{
					
			// 	})
			// },
			detailList(){
				uni.showLoading({
					title:"加载中"
				});
				let orderObj={
					orderNo:this.orderNo,
					...this.queryParams
				}
				this.request('orderapi/orderDetils',orderObj,'POST',{}).then((res)=>{
					uni.hideLoading();
					this.orderDetaillist=[...this.orderDetaillist,...res.data.data.list];
					this.totalNum=res.data.data.totalCount;
				})
			},
			onReachBottom(){
				if(this.queryParams.pageNum*this.queryParams.pageSize>=this.totalNum){
					this.$refs.uToast.show({
						message: '加载完成',
						type: 'default'
					});
				}else{
					this.queryParams.pageNum += 1;
					this.detailList();
				}
			},
			godetailSearch(){
				
			},
		}
	}
</script>
<style lang="scss" scoped>
	.searchBtn {
		flex-shrink: 0;
		width: 100%;
		height: 120rpx;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding: 0 20rpx;
		background: #fff;

		::v-deep .u-search__action {
			color: #000 !important;
		}

		// .screening {
		// 	width: 10%;
		// 	margin-left: 10rpx;
		// 	font-weight: 700;
		// 	color: #f6338f;
		// }
	}
	.total{
		height: 50rpx;
		line-height: 50rpx;
		width: 100%;
		padding: 10rpx 30rpx;
		.totalColor{
			font-size: 28rpx;
			color: #696969;
		}
		.detailContent{
			width: 100%;
			// padding: 20rpx 0;
			display: flex;
			flex-direction: column;
			.detailItem{
				min-height: 200rpx;
				display: flex;
				flex-direction: column;
				justify-content: center;
				padding: 20rpx 30rpx;
				background-color: #fff;
				border-radius: 16rpx;
				margin-top: 10rpx;
				.detailSingle{
					display: flex;
					justify-content: flex-start;
					.detailHeader{
						width: 24%;
						margin-right: 10rpx;
						color: #696969;
					}
					.detailValue{
						width: 70%;
						color:#252623;
					}
				}
			}
		}
	}
	.empty{
		height: 500rpx;
		line-height: 500rpx;
		width: 100%;
		text-align: center;
	}
</style>